import { Component,NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-qualification-priview',
  templateUrl: './qualification-priview.page.html',
  styleUrls: ['./qualification-priview.page.scss'],
})
export class QualificationPriviewPage implements OnInit {

  formPreviewData: any;
  priviousData: any;
  loginData: any;

  constructor(private router:Router,private navctrl:NavController,private sendData:DataTransferService,
    private zone:NgZone,private localStorage:LocalStorageService,private apiservices:ApiservicesService) { 
    

  }


  ionViewWillEnter(){
    this.formPreviewData=this.sendData.myParam;
    this.priviousData=this.sendData.alldata;
    console.log("resend OTP by Voice Call, api response=1=",this.priviousData);
    console.log("resend OTP by Voice Call, api response==", this.formPreviewData);
    if(this.priviousData==0 || this.formPreviewData.module_id==15){
      this.priviousData=0;
      this.aboartDetail();
      
    }else{
   
    }
  }


  ngOnInit() {
  } 

  goToback(){
    this.navctrl.pop();
  }

  goToNext(){
    this.apiservices.showLoader();
    this.localStorage.getStorage().then(stored=>{
      this.loginData=stored;
      console.log("resend OTP by Voice Call, api response==",this.priviousData)
    this.zone.run(()=>{
      let apiKey={
        "employee_id":this.loginData.employeeID,
        "experience_type":this.formPreviewData.formData.experience_type,
        "highest_degree": this.formPreviewData.formData.highest_degree,
        "college_name":this.formPreviewData.formData.college_name,
        "fav_dish":this.formPreviewData.formData.fav_dish,
        "hobby":this.formPreviewData.formData.hobby,
        "holiday_destination":this.formPreviewData.formData.holiday_destination,
        "fav_sports":this.formPreviewData.formData.fav_sports,
        "experience":this.formPreviewData.formData.experience,
        "last_company":this.formPreviewData.formData.last_company,
        "last_designation":this.formPreviewData.formData.last_designation,
        "completion_year":this.formPreviewData.formData.completion_year
      };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.previewAboardSubmitApi, apiKey).subscribe((result) => {
        this.apiservices.hideLoader();
        console.log("resend OTP by Voice Call, api response==",result);
        if(result.status==1 || result.status=='1'){
          // this.sendData.alldata=result;
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
          this.router.navigate(['/tabs']);
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    });
  });
  }

  aboartDetail(){
    this.apiservices.showLoader();
    this.zone.run(()=>{
      this.localStorage.getStorage().then(stored=>{
      let apiKey={
        "post_id":this.formPreviewData.auto_id,
        "employee_id":stored.employeeID,
      };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.aboardDetailApi, apiKey).subscribe((result) => {
        this.apiservices.hideLoader();
        if(result.status==1 || result.status=='1'){
          this.formPreviewData={apiRes:result}
          console.log("resend OTP by Voice Call, api response==",this.formPreviewData);
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    
  });
});
  }



  ionViewWillLeave(){
    this.apiservices.showCommentBox=false;
    console.log('aboartDetail==> ', this.formPreviewData);
    this.formPreviewData.like_status= this.formPreviewData.apiRes.data.like_status;
    this.formPreviewData.total_comments= this.formPreviewData.apiRes.data.total_comments;
    this.formPreviewData.total_likes= this.formPreviewData.total_likes;
    this.formPreviewData.comment_setting= this.formPreviewData.apiRes.data.comment_setting;
    this.formPreviewData.like_setting= this.formPreviewData.apiRes.data.like_setting;
  }
}
